var zh_default = 'n'; //
var zh_choose = 's'; //
var zh_class = 'zh_click'; //classidclass + s/t/n 
var zh_style_active = 'font-weight:bold; color:green;'; //
var zh_style_inactive = 'color:blue;'; //
var zh_s = '';
var zh_t = '';
String.prototype.tran = function() {					//
	var s1,s2;
	if (zh_choose == 't') {
		s1 = zh_s;
		s2 = zh_t;
	}else if(zh_choose == 's') {
		s1 = zh_t;
		s2 = zh_s;
	}else {
		return this;
	}
	var a = '';
	var l = this.length;
	for(var i=0;i<this.length;i++){
		var c = this.charAt(i);
		var p = s1.indexOf(c)
		a += p < 0 ? c : s2.charAt(p);
	}
	return a;
}

function zh_tranBody(obj) {
	var o = (typeof(obj) == "object") ? obj.childNodes : document.body.childNodes;              //
	 console.log(o)
	for (var i = 0; i < o.length; i++) {
		var c = o.item(i);
		if ('||BR|HR|TEXTAREA|SCRIPT|'.indexOf("|"+c.tagName+"|") > 0) continue;
		if (c.className == zh_class) {
			if (c.id == zh_class + '_' + zh_choose) {
				c.setAttribute('style', zh_style_active);
				c.style.cssText = zh_style_active;
			}else {
				c.setAttribute('style', zh_style_inactive);
				c.style.cssText = zh_style_inactive;
			}
			continue;
		}
		if (c.title != '' && c.title != null) c.title = c.title.tran();
		if (c.alt != '' && c.alt != null) c.alt = c.alt.tran();             //alt
		if (c.placeholder != '' && c.placeholder != null) c.placeholder = c.placeholder.tran();         //placeholder
		if (c.tagName == "INPUT" && c.value != '' && c.type != 'text' && c.type != 'hidden' && c.type != 'password') c.value = c.value.tran();
		if (c.nodeType == 3) {
			c.data = c.data.tran();
		}else{
			zh_tranBody(c);
		}
	}
}

export default function zh_tran(go) {
	if (go) zh_choose = go;
	if (go == 'n') {
		window.location.reload();
	}else {
		zh_tranBody();
	}
}


